import Endpoint from '@/services/Endpoint'

export default {
  chequeTypes (payload) {
    return Endpoint().get('/s1/cheque/chequeType', {
      params: payload
    })
  },
  createChequeType (payload) {
    return Endpoint().post('/s1/cheque/chequeType', payload)
  },
  modifyChequeType (payload) {
    return Endpoint().patch('/s1/cheque/chequeType', payload)
  }
}
